function App() {
  let [trans, setTranslated] = useState('')
  let [seco, setSecond] = useState('ru')
  let [first, setFirst] = useState('en')

  let text = React.createRef();

  const translate = (firstV, secondV = null)=>{
    let fr;
    let sec;
    if (firstV != null){
      fr = firstV
    } else {
      fr = first
    }

    if (secondV != null){
      sec = secondV
    } else {
      sec = seco
    }

    let txt = text.current.value

    let from = []
    if (fr === "en"){
      from = [...en]
    } else if (fr === "uk"){
      from = [...uk]
    } else if (fr === "ru"){
      from = [...ru]
    }

    let second = []
    if (sec === "en"){
      second = [...en]
    } else if (sec === "uk"){
      second = [...uk]
    } else if (sec === "ru"){
      second = [...ru]
    }

    let res = ''

    if (fr === sec){
      res = txt
    } else {
      for (let i = 0; i < txt.length; i++){
        let index = from.indexOf(txt[i])
        if (index === -1){
          res += txt[i]
        } else {
          res += second[index]
        }
      }
    }
    setTranslated(res)
  }

  return (
    <div className="App">
      <center>
        <br/>
        <br/>
        <h3 style={{marginLeft: '10px', marginRight: '10px'}}>I used&nbsp;
          <select onChange={(e)=>{
            setFirst(e.target.value)
            translate(e.target.value, null)
          }} style={{display: "inline-block", width: '90px'}} className="form-control">
            <option value="en">english</option>
            <option value="uk">ukranian</option>
            <option value="ru">russian</option>
          </select>&nbsp;keyboard, but really wanted to use&nbsp; 
          <select onChange={(e)=>{
            setSecond(e.target.value)
            translate(null, e.target.value)
          }} style={{display: "inline-block", width: '90px'}} className="form-control">
            <option value="ru">russian</option>
            <option value="en">english</option>
            <option value="uk">ukranian</option>
          </select>&nbsp;keyboard.</h3>
          <br/>
          <textarea onChange={translate} ref={text} placeholder="Text:" style={{height: '300px', width: '60%', minWidth: '300px'}} className="form-control"></textarea>
          <br/>
          <hr/>
          <br/>
          <div style={{height: '300px', width: '60%'}}>{trans}</div>
        </center>
    </div>
  );
}

export default App;
